.App {
  width: 100vw;
  height: 100vh;
  font-family: "Rubik", sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

.Navbar {
  background-color: #021e39;
  width: 100%;
  height: 80px;
  display: flex;
}

.Navbar .LeftSide {
  flex: 50% 1;
  display: flex;
  background-color: white;
  justify-content: left;
  align-items: center;
}

.Navbar .LeftSide .Link {
  max-height: 80px;
}

.Navbar .LeftSide .Link a {
  max-height: 80px;
  text-decoration: none;
  color: black;
  font-size: 25px;
  margin-left: 15px;
  font-weight: bold;
}

.Navbar .LeftSide button {
  display: none;
}
.Navbar .LeftSide #hidden {
  display: flex;
  margin-left: 20px;
}
/* #021e39 */
.Navbar .RightSide {
  flex: 50% 1;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: right;
  padding-right: 25px;
}

.Navbar .RightSide a {
  width: 5%;
  height: auto;
}

.Navbar .RightSide img {
  top: -60px;
  position: absolute;
  left: 0px;
  width: 12%;
  height: auto;
}

.RightSide input {
  width: 220px;
  height: 30px;
  border: none;
  border-radius: 4px;
  font-size: 20px;
  padding-left: 15px;
  color: back;
}

.RightSide button {
  height: 32px;
  width: 70px;
  font-size: 16px;
}

button:hover {
  cursor: pointer;
}

@media only screen and (max-width: 1605px) {
  .Navbar .RightSide img {
    top: -30px;
  }
}

@media only screen and (max-width: 1088px) {
  .Navbar .LeftSide .Link a {
    font-size: 20px;
  }
}

@media only screen and (max-width: 839px) {
  .Navbar .LeftSide .Link a {
    font-size: 15px;
  }

  .Navbar .LeftSide .button {
    display: none;
  }
}

@media only screen and (max-width: 670px) {
  .Navbar .LeftSide button {
    display: flex;
    margin-left: 20px;
  }

  .Navbar .LeftSide .Link {
    display: none;
  }

  .Navbar .LeftSide #hidden {
    position: absolute;
    left: 0px;
    top: 80px;
    max-height: 230px;
    width: 100%;
    background: white;
    margin-left: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .Navbar .LeftSide #hidden a {
    margin: 10px;
  }

  .Navbar .RightSide img {
    top: 20px;
  }
}

@media only screen and (max-width: 375px) {
  .Navbar .RightSide button {
    display: none;
    margin-left: 0px;
  }

  .Navbar .LeftSide button {
    margin-left: 5px;
  }
}

.container{
  display:grid;
  place-items: center;
  text-align: center;
}

.rating-container{
  position: relative;
  display:grid;
  width: 400px;
  padding: 20px 30px;
  border: 1px solid #444;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.rating-container .post{
  display: none;
}

.rating-container .text{
  font-size: 25px;
  color: #666;
  font-weight: 500;
}

.rating-container .edit{
  position: absolute;
  right: 10px;
  top: 5px;
  font-size: 16px;
  color: #666;
  font-weight: 500;
  cursor: pointer;
}

.rating-container .edit:hover{
  text-decoration: underline;
}

.rating-container .star-widget input{
  display: none;
}

.star-widget label{
  font-size: 40px;
  color: #444;
  padding: 10px;
  float: right;
  transition: all 0.2s ease;
}

input:not(:checked) ~ label:hover,
input:not(:checked) ~ label:hover ~ label{
  color: #fd4;
}

input:checked ~ label{
  color: #fd4;
}

input#rate-5:checked ~ label{
  color: #fe7;
  text-shadow: 0 0 20px #952;
}

#rate-1:checked ~ form header:before{
  content: "I just hate it ";
}

#rate-2:checked ~ form header:before{
  content: "I don't like it ";
}

#rate-3:checked ~ form header:before{
  content: "It is awesome ";
}

#rate-4:checked ~ form header:before{
  content: "I just like it ";
}

#rate-5:checked ~ form header:before{
  content: "I just love it ";
}

.rating-container form{
  display: none;
}

input:checked ~ form{
  display: block;
}

form header{
  width: 100%;
  font-size: 25px;
  color: #fe7;
  font-weight: 500;
  margin: 5px 0 20px 0;
  text-align: center;
  transition: all 0.2s ease;
}

.btn{
  background-color: black;
  position: relative;
  right: 3%;
  margin: 20px;
}

.btn button{
  border: 1px solid #444;
  outline: none;
  background: #222;
  color: #999;
  font-size: 17px;
  font-weight: 500;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn button:hover{
  background: #1b1b1b;
}
.grid-container{
    display: grid;
    grid-template-columns: auto auto;
    padding: 10px;
    grid-gap: 50px;
}
.grid-item{
    background-color: aliceblue;
    font-size: 30px;
