}
.Header {
  position: relative;
  background-color: rgb(255, 255, 255);
  height: 100vh;
}
/* position: relative; */
.HeaderImage {
  position: absolute;
  right: 0;
  bottom: 15%;
  max-width: 60%;
  height: auto;
}

.HeaderTexts {
  position: absolute;
  top: 20%;
  left: 11%;
  text-align: left;
  max-width: 550px;
}
.HeaderHeading {
  font-size: 58px;
}

.HeaderSubtitle {
  font-size: 32px;
}

